import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import InfoCountTotalCard from '../../../components/common/InfoCard/index';
import {
  getQuickLinkGroupGetPoliticStudentNo,
  getQuickLinkGroupGetUnionStudentNo,
  getQuickLinkGroupGetGroupNo,
  getQuickLinkGroupGetGroupMemberNo,
} from '../../../services/quicklink';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'Đảng viên',
    service: getQuickLinkGroupGetPoliticStudentNo,
    url: '/party-mem/list',
  },
  {
    title: 'Đoàn viên',
    service: getQuickLinkGroupGetUnionStudentNo,
    url: '/unions-mem/list',
  },
  {
    title: 'Hội',
    service: getQuickLinkGroupGetGroupNo,
    url: '/group/list',
  },
  {
    title: 'Hội viên',
    service: getQuickLinkGroupGetGroupMemberNo,
    url: '/association-mem/list',
  },
];

class DashboardGroup extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCountTotalCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardGroup {...props} />
  </Page>
);



